.container {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-color: #02f3ab;
}
.title {
    font-size: 60px;
    font-family: "Montserrat Thin", sans-serif;
    font-weight: bolder;
    text-transform: uppercase;
    letter-spacing: 0.125em;
    padding: 40px 0 40px 0;

}
.formBlock {
    width: 80%;
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid white;
    border-radius: 5px;
    margin: 0 auto;
    flex-wrap: wrap;
}
.form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    margin: 0 auto;
}
